.colorBoxes{ 
  display: flex;
  align-items: center;
  justify-self: center;
  margin: 0 20px 0 0;
}

.colorBoxes div{
    width: 60px;
    height: 40px;
    margin: 0 10px;
    box-shadow: 3px 3px 3px grey;
    cursor: pointer;
    border-radius: 100px;
}

.colorBoxes .whiteBlack{
    background: linear-gradient(to top, white, black);  
}
.colorBoxes .blackWhite{
    background: linear-gradient(to top, black, white);  
}
.colorBoxes .blackOrange{
    background: linear-gradient(to top, black, orange);  
}
.colorBoxes .greyWhite{
    background: linear-gradient(to top, grey, white);  
}
.colorBoxes .greenWhite{
    background: linear-gradient(to top, green, white);  
}